  border-radius: 8px;
  border: none;
  padding: 5px;
  cursor: pointer;
  opacity: .5;
  outline: none;
  transition: all .2s ease;
}
.up:hover, .down:hover, .right:hover, .left:hover, .jump:hover, .run:hover {
  opacity: 1!important;
}
.up {
  position: absolute;
  bottom: 220px;
  right: 110px;
  height: 100px;
  width: 100px;
}
.down {
  position: absolute;
  bottom: 0;
  right: 110px;
  height: 100px;
  width: 100px;
}
.run {
  position: absolute;
  bottom: 0;
  right: 0;
  height: 100px;
  width: 100px;
}
.right {
  position: absolute;
  bottom: 110px;
  right: 0;
  height: 100px;
  width: 100px;
}
.left {
  position: absolute;
  bottom: 110px;
  right: 220px;
  height: 100px;
  width: 100px;
}
.jump {
  position: absolute;
  bottom: 110px;
  right: 110px;
  height: 100px;
  width: 100px;
}
</style>
